.img_Cntnr {
    height: 50vh;
    width: 35vw;
    transition: 0.3s;
    /* border: .5px solid blue;  */
    position: relative;
}

.img_Cntnr:hover {
    width: 40vw;
    transition: 0.8s;
}
.img_Cntnr img {
    height: 100%;
    width: 100%;
    /* opacity: 0.5; */
    /* background: rgba(75, 89, 146, 0.5); */
    object-fit:cover;
}


.title-n-rightBtn_Cntnr {
    width: 100%;
    margin-top: 5px;
    padding: 0 15px;
    /* border: 1px solid red; */
    position: absolute;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.title-n-rightBtn_Cntnr h2 {
    color: white;
    font-size: 18px;
    font-weight: 300;
    text-shadow: 1.5px 1.5px 2px black;
    /* border: 1px solid blue; */
}

.title-n-rightBtn_Cntnr .rightBtn {
    height: 22px;
    width: 22px;
    padding-left: 2px;
    border: 0.5px solid #EAEDED;
    border-radius: 100%;
    box-shadow: 0 0 3px -1px black;
    background-color: #EAEDED;
    display: none;
}
.img_Cntnr:hover .rightBtn  {
    display: block;
}

.img_Cntnr img {
    height: 100%;
    width: 100%;
    cursor: pointer;
    object-fit: cover;
}

