footer {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.footer-list_Cntnr {
    width: 100%;
    /* border: .5px solid red; */
    display: flex;
    
}

.footer-list ul li {
    margin: 2px 5px;
    padding: 2px 4px;
    font-size: 12px;
    font-weight: 300;
    color: aliceblue;
    list-style: none;
}

.copy-right-strip_Cntnr {
    height: 30px;
    width: 100%;
    /* border: 0.5px solid blue; */
    background-color: black;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    bottom: -6px;
}

.copy-right-strip_Cntnr .copy-right-strip {
    font-size: 14px;
    font-weight: 300;
    color: silver;
}