.navbar {
    height: 60px;
    width: 100%;
    /* border-bottom: .5px solid black; */
    box-shadow: 0 0 10px -2px black;   /* y-axis x-axis blur spred color */
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.home-logo_Cntnr {
    height: 100%;
    margin-left: 6px;
    /* border: .5px solid green; */
    display: flex;
    align-items: center;
    justify-content: center;
}
.home-logo_Cntnr .home-logo {
    height: 47px;
    width: 47px;
    font-size: 22px;
    font-weight: 20;
    text-decoration: none;
    color: #006c75;
    color: gainsboro;
    border-radius: 100%;
    border: .2px solid rgb(0, 34, 36);
    background-color: rgb(14 63 67);
    display: flex;
    justify-content: center;
    align-items: center;
}

.navbar ul {
    height: 100%;
    /* border: .5px solid black; */
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.title_Cntnr {
    margin: 5px 22px;
    list-style: none;
    border-radius: 2px;
    transition: 1s;
    /* border: .5px solid red; */
}

.title_Cntnr .title {
    padding: 0 7.5px;
    padding-top: 2.5px;
    color: black;
    text-decoration: none;
    /* border: .5px solid blue; */
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.title_Cntnr .title span {
    font-size: 12px;
    font-weight: 400;
    /* border: .5px solid black; */
}

.login-title_Cntnr {
    height: 100%;
    margin-right: 20px;
    /* border: .5px solid green; */
    display: flex;
    align-items: center;
    justify-content: center;
}

.login-title {
    margin: 5px;
    padding: 1px 7px;
    font-size: 15px;
    color: black;
    text-decoration: none;
    /* border: .5px solid blue; */
    display: flex;
    align-items: center;
    justify-content: center;
}

.login-title .login-icon {
    height: 23px;
    width: 23px;
    /* border: 0.5px solid rebeccapurple; */
}